.moviesGrid_hvrBounceIn__2rPle {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
}
.moviesGrid_hvrBounceIn__2rPle:before {
  content: '';
  position: absolute;
  border: #e1e1e1 solid 4px;
  top: -12px;
  right: -12px;
  bottom: -12px;
  left: -12px;
  opacity: 0;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
}
.moviesGrid_hvrBounceIn__2rPle:hover:before, .moviesGrid_hvrBounceIn__2rPle:focus:before, .moviesGrid_hvrBounceIn__2rPle:active:before {
  -webkit-animation-name: moviesGrid_hvrBounceIn__2rPle;
  animation-name: moviesGrid_hvrBounceIn__2rPle;
}

.moviesGrid_hvrBounceIn__2rPle:hover,
.moviesGrid_hvrBounceIn__2rPle:focus,
.moviesGrid_hvrBounceIn__2rPle:active {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
}


@media (max-width: 500px) {
